.cases {
	padding-top: 75px;
	padding-bottom: 50px;
}

.cases__title {
	font-weight: 500;
	font-size: clamp(24px, 3.5vw, 40px);
	line-height: 1.29;
	text-align: center;
	color: #333333;
	margin-bottom: clamp(25px, 4.5vw, 60px);
}

.cases__list {
	display: flex;
	flex-wrap: wrap;
	align-items: top;
	justify-content: center;
	gap: 30px;
}

.cases__list-item {
	max-width: 370px;
	width: 100%;
	text-align: center;
	span {
		display: inline-block;
		font-family: Roboto;
		font-style: normal;
		font-weight: 400;
		font-size: 22px;
		line-height: 1.5;
		text-align: center;
		color: #333333;
	}
}

.cases__list-item:nth-child(4) span {
	width: 230px;
}
.cases__list-item:nth-child(5) span {
	width: 270px;
}

.cases__list-item--icon::before {
	flex: 0 0 auto;
	display: block;
	content: "";
	width: 60px;
	height: 60px;
	background-image: url("../img/cases-icon2.png");
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	margin-bottom: 16px;
	margin-left: auto;
	margin-right: auto;
}

@media (max-width: 978px) {
	.cases {
		padding-top: 25px;
		padding-bottom: 25px;
	}
	.cases__list {
		grid-template-columns: 1fr 1fr;
	}
}
@media (max-width: 768px) {
	.cases__list {
		grid-template-columns: 1fr;
	}
	.cases__list-item {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
	.cases__list-item {
		span {
			text-align: center;
		}
	}
}
